import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

import Link from 'components/Link'
import Section from '../IntersectedSection'

/*
TO CHECK
*/

const MoreInfo = ({ className, ...props }) => (
  <Section title="More stuff" className={className} {...props}>
    <div className="columns">
      <div className="column is-6">
        <Link href="/projects.html">
          <h2 className="subtitle">Projects</h2>
          <p>More info</p>
        </Link>
      </div>
      <div className="column is-6">
        <Link href="/publications.html">
          <h2 className="subtitle">Publications</h2>
          <p>More info</p>
        </Link>
      </div>
    </div>
    <div className="columns">
      <div className="column is-6">
        <Link href="/research_projects.html">
          <h2 className="subtitle">R&amp;D Projects</h2>
          <p>More info</p>
        </Link>
      </div>
      <div className="column is-6">
        <Link href="/teaching.html">
          <h2 className="subtitle">Teaching</h2>
          <p>More info</p>
        </Link>
      </div>
    </div>
  </Section>
)

MoreInfo.propTypes = {
  className: PropTypes.string
}

export default styled(MoreInfo)`
  background: #ccc;

  h1 {
    color: #22a39f;
  }

  h2 {
    color: #fff;
    font-weight: bold;
  }
`
